@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;500&family=Open+Sans&display=swap');

html,
body {
    margin: 0% !important;
    border: 1px solid black
}

header {
    height: 150px;
}

header .row {
    width: 19%;
    height: 80px;
    position: relative;
}

header .logo {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media only screen and (max-width: 1870px) {
    .intro-content {
        height: 531px !important;
        width: 694px !important ;
    }
}

@media only screen and (max-width: 1500px) {
    header .row {
        width: 30%;
    }

    .intro-content {
        height: 530px !important;
        width: 679px !important;
    }
}

header .logo-icon {
    color: transparent;
    width: 206px;
    height: 100px;
    background: url("./images/logo.svg") no-repeat left;
    background-size: 120px 127px;
    -moz-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    margin-top: 25px;
}

* {
    box-sizing: border-box;
}

.container {
    padding: 64px;
    margin-left: 90px;
}

.container .row:after {
    content: "";
    display: table;
    clear: both
}

.container .column-66 {
    float: left;
    width: 50%;
    padding: 20px;
}

.container h1 {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 50px;
    line-height: 83px;
    color: #6C6C6C;
    margin-top: 130px;
}

.container h5 {
    margin-top: 52px;
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 23px;
    line-height: 42px;
    color: #6C6C6C;
}

.container strong {
    font-weight: bolder;
    color: rgba(38, 50, 56, 1);
}


.container .column-33 {
    float: left;
    width: 50%;
    padding: 20px;
}

.container .column-99 {
    width: 100%;
    float: left;
    padding: 20px;
    text-align: center;
}

@media only screen and (max-width: 1000px) {
    header .row {
        width: 40%;
    }

    .container {
        padding: 30px;
        margin-top: 0px;
        margin-left: 0;
    }

    .container .column-66 {
        float: left;
        width: 39%;
        padding: 20px;
    }

    .container h1 {
        font-size: 30px;
        line-height: 47px;
    }

    .container h5 {
        font-size: 20px;
        line-height: 33px !important;
    }

    .container .column-33 {
        float: left;
        width: 50%;
        padding: 20px;
    }

    .intro-content {
        height: 492px !important;
        width: 579px !important;
    }

    .container .column-99 {
        width: 100%;
        float: left;
        padding: 0px;
        text-align: center;
    }

    .column-99 .items {
        margin-top: 120px;
        display: grid;
        grid-template-columns: 24% 24% 30% 22%;
        align-items: center;
    }

    .column-99 .item-1 {
        width: 160px !important;
        height: 160px !important;
    }

    .column-99 .item-2 {
        width: 160px !important;
        height: 160px !important;
    }

    .column-99 .item-3 {
        width: 200px !important;
        height: 100px !important;
    }

    .column-99 .item-4 {
        width: 200px !important;
        height: 56px !important;
    }

    .contact .column-66 {
        width: 50%;
    }

    .column-66 .Contact-Us {
        height: 371px !important;
        width: 400px !important;
    }

    .why .column-66 {
        width: 40% !important;
    }

    .why .column-33 {
        width: 40%;
        margin-top: 20%;
    }

    .why .page {
        height: 495px !important;
        width: 408px !important;
    }
}

.column-99 .item-1 {
    width: 200px;
    height: 200px;
}

.column-99 .item-2 {
    width: 150px;
    height: 150px;
}

.column-99 .item-3 {
    width: 250px;
    height: 100px;
}

.column-99 .item-4 {
    width: 250px;
    height: 56px;
}

.column-99 .items {
    margin-top: 120px;
    display: grid;
    grid-template-columns: 26% 26% 26% 22%;
    align-items: center;
}

.intro-content {
    height: 650px;
    width: 870px;
}

.column-66 .Contact-Us {
    margin-top: 200px;
    height: 500px;
    width: 600px;
}

.contact {
    background: url("./images/bg.svg") no-repeat center;
    background-size: 1000px 1000px;
}

.contact .column-33 {
    margin-top: 5%;
}

.under-box {
    background: #36033F;
    height: 15px;
    width: 100px;
    border-radius: 17px;
}

.contact .column-33 h5 {
    line-height: 55px;
}

.butt {
    padding: 13px 48px;
    text-decoration: none;
    text-align: center;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    border-radius: 58.5px;
    background: #50105B;
    transition: 0.5s;
}

.butt:hover {
    transition: 0.5s;
    background: #7a198b;
}

.why .column-66 {
    margin-top: 100px;
    margin-left: 10%;
    width: 35%;
}

.why .page {
    height: 350px;
    width: 650px;
}

.why .column-33 {
    width: 40%;
    margin-top: 20%;
}

.why .butt {
    margin-left: 40%;
}

.why .under-box {
    height: 12px;
    width: 75px;
    margin-left: 54%;
    margin-top: 15%;
}

.container .why h5 {
    margin-top: 15px;
}

footer {
    margin-top: 233px;
    width: 100%;
    height: 350px;
    background: url("./images/footer-bg.png");
    background-size: 100% 100%;
}

footer .footer-bottom {
    display: flex;
    justify-content: space-evenly;
}

footer .company {
    margin-top: 5%;
}

footer .services {
    margin-top: 5%;
}

footer h1 {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
    color: #FFFFFF;
}

footer h5 {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 20px;
    color: #FFFFFF;
}